.profile-wrapper {
  width: 100%;
  height: auto;
  padding-bottom: 40px;
  border-radius: 10px;
  margin-bottom: 120px;
}

.profile-title {
  text-align: center;
  margin-left: 20px;
  font-size: 40px;
  text-decoration: underline;
  color: #bcf8ec;
}

.profile-dev {
  text-align: center;
  color: #bcf8ec;
}

.profile-content {
  width: 100%;
  margin: 0 auto;
  padding: 40px 0;
  border-radius: 10px;
}

.profile-container {
  position: relative;
  top: 100px;
  width: 90%;
  margin: 0 auto 125px auto;
  border-radius: 10px;
  box-sizing: border-box;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  background: url('../images/About.jpg');
  background-position: right;
}

.profile-description {
  font-size: 26px;
  max-width: 40%;
  margin: 0 auto;
  min-width: 250px;
  text-align: center;
  color: black;
  padding-top: 20px;
  color: #bcf8ec;
}

.profile-email, .github {
  padding: 12px 18px;
  border: 2px solid #bcf8ec;
  font-weight: bold;
  border-radius: 10px;
  color: #bcf8ec;
  text-decoration: none;
  text-transform: uppercase;
  width: 150px;
  margin: 20px auto 0 auto;
  display: flex;
  justify-content: center;
}

.profile-email:hover, .github:hover {
  color: #242424;
  background-color: white;
  background-size: cover;
}

@media screen and (max-width: 1496px) {
  .article-wrapper {
    height: 875px;
  }
}

@media screen and (max-width: 1024px) {
  .article-wrapper {
    height: 1400px;
  }

  .article-content {
  }
}

@media screen and (max-width: 1200px) {
  .article-wrapper {
    height: 1400px;
  }

  .article-content {
  }
}

@media screen and (max-width: 768px) {
  .article-wrapper {
    height: 1500px;
  }

  .article-content {
    grid-template-rows: repeat(4, 310px);
    padding-bottom: 200px;
  }
}

@media screen and (max-width: 450px) {
  .article-wrapper {
    height: auto;
  }

  .article-content {
    padding-bottom: 0;
  }

  .article-title {
    font-size: 30px;
    text-align: center;
  }

  .article-description {
    padding-top: 0;
    margin: 0 auto;
    text-align: center;
    max-width: 90%;
  }
}
