* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Oswald', sans-serif;
}

.home,
.parks,
.about,
.sign-up,
.venice-beach,
.banff,
.vans,
.lake-cunningham,
.peitruss,
.linda-vista,
.san-luis {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  width: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #bcf8ec;
  font-size: 100px;
  text-transform: uppercase;
}

.venice-beach,
.banff,
.vans,
.lake-cunningham,
.peitruss,
.linda-vista,
.san-luis,
.about {
  margin: 0 auto 0 auto;
  width: 98%;
}

.parks {
  background-image: url('images/Sunset.jpg');
  border-radius: 10px;
}

.about {
  background-image: url('images/About.jpg');
  border-radius: 10px;
}

.sign-up {
  background-image: url('images/Sign-up-background.jpg');
  border-radius: 10px;
}

.venice-beach {
  background-image: url('images/Venice-beach-skatepark.jpg');
  border-radius: 10px;
}

.banff {
  background-image: url('images/banff.jpg');
  border-radius: 10px;
}

.vans {
  background-image: url('images/Vans-skatepark.jpg');
  border-radius: 10px;
}

.lake-cunningham {
  background-image: url('images/lakecunningham.jpg');
  border-radius: 10px;
}

.peitruss {
  background-image: url('images/Peitruss.jpg');
  border-radius: 10px;
}

.linda-vista {
  background-image: url('images/linda-vista.jpg');
  border-radius: 10px;
}

.san-luis {
  background-image: url('images/slo.webp');
  border-radius: 10px;
}

/* Rules for GoogleMapAPI */
.Map {
  height: 350px;
  width: 90%;
  margin: 150px auto 30px auto;
  box-sizing: border-box;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
  padding: 20px;
  border-radius: 10px;
}

.map-button {
  width: 140px;
  margin: 0 auto 50px auto;
}

@media screen and (max-width: 450px) {
  .venice-beach,
  .banff,
  .vans,
  .lake-cunningham,
  .peitruss,
  .linda-vista,
  .san-luis,
  .services {
    font-size: 66px;
  }

  .MapDiv {
    padding-top: 0;
  }
}
