:root {
  --primary: #fff;
}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
  text-transform: uppercase;
}

.btn--primary {
  background-color: var(--primary);
  color: #bcf8ec;
  border: 1px solid var(--primary);
  border-radius: 10px;
}

.btn--outline {
  background-color: transparent;
  color: #bcf8ec;
  padding: 8px 20px;
  border: 1px solid #bcf8ec;
  transition: all 0.3s ease-out;
  border-radius: 10px;
}

.btn--parks {
  background-color: #242424;
  color: #bcf8ec;
  padding: 8px 20px;
  border: 4px solid white;
  border-radius: 10px;
  transition: all 0.3s ease-out;
  box-sizing: border-box;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
  rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.btn--medium {
  padding: 8px 20px;
  font-size: 20px;
}

.btn--large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn--medium:hover,
.btn--large:hover {
  background: var(--primary);
  color: #242424;
  transition: all 0.3s ease-out;
}
