.article-wrapper {
  width: 100%;
  height: auto;
  padding-bottom: 40px;
}

.article-title {
  text-align: left;
  margin-left: 20px;
  font-size: 40px;
  text-decoration: underline;
  grid-column: 1 / -1;
  grid-row: 1 / 2;
}

.article-content {
  width: 100%;
  margin: 0 auto;
  padding: 40px 0;
  border-radius: 10px;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-gap: 20px;
}

.park-container {
  position: relative;
  top: 100px;
  width: 90%;
  padding-top: 12px;
  margin: 0 auto 0 auto;
  border-radius: 10px;
  box-sizing: border-box;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.article-description {
  font-size: 26px;
  max-width: 40%;
  min-width: 250px;
  text-align: left;
  margin-left: 20px;
  color: black;
  grid-column: 1 / -1;
  grid-row: 1 / -1;
  padding-top: 100px;
}

.article-pictures {
  width: 395px;
  height: 270px;
  border-radius: 10px;
  box-sizing: border-box;
  box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px,
    rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px,
    rgba(0, 0, 0, 0.09) 0px 32px 16px;
}

.img-1 {
  grid-column: 3 / 3;
  grid-row: 1 / 1;
}
.img-2 {
  grid-column: 4 / 4;
  grid-row: 1 / 1;
  margin-right: 20px;
}
.img-3 {
  grid-column: 3 / 3;
  grid-row: 2 / 2;
}
.img-4 {
  grid-column: 4 / 4;
  grid-row: 2 / 2;
  margin-right: 20px;
}

@media screen and (max-width: 1496px) {
  .article-wrapper {
    height: 875px;
  }

  .article-pictures {
    width: 350px;
    height: 300px;
  }

  .img-1,
  .img-2 {
    margin-top: 100px;
    margin-bottom: 24px;
  }
}

@media screen and (max-width: 1024px) {
  .article-wrapper {
    height: 1400px;
  }

  .article-content {
    grid-template-rows: repeat(4, 1fr);
  }

  .article-pictures {
    width: 450px;
  }

  .img-1 {
    grid-column: 4 / 5;
    grid-row: 1 / 2;
    margin-top: 0;
  }

  .img-2 {
    grid-column: 4 / 5;
    grid-row: 2 / 3;
    margin-top: 0;
  }

  .img-3 {
    grid-column: 4 / 5;
    grid-row: 3 / 4;
  }

  .img-4 {
    grid-column: 4 / 5;
    grid-row: 4 / 5;
  }
}

@media screen and (max-width: 1200px) {
  .article-wrapper {
    height: 1400px;
  }

  .article-content {
    grid-template-rows: repeat(4, 1fr);
  }

  .article-pictures {
    width: 450px;
  }

  .img-1 {
    grid-column: 4 / 5;
    grid-row: 1 / 2;
    margin-top: 0;
  }

  .img-2 {
    grid-column: 4 / 5;
    grid-row: 2 / 3;
    margin-top: 0;
  }

  .img-3 {
    grid-column: 4 / 5;
    grid-row: 3 / 4;
  }

  .img-4 {
    grid-column: 4 / 5;
    grid-row: 4 / 5;
  }
}

@media screen and (max-width: 768px) {
  .article-wrapper {
    height: 1500px;
  }

  .article-content {
    grid-template-rows: repeat(4, 310px);
    padding-bottom: 200px;
  }

  .article-pictures {
    width: 350px;
    margin-top: 100px;
    height: 300px;
  }
}

@media screen and (max-width: 450px) {
  .article-wrapper {
    height: auto;
  }

  .article-content {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 50px 1fr 310px 310px 310px 310px;
    padding-bottom: 0;
  }

  .article-title {
    font-size: 30px;
    text-align: center;
  }

  .article-description {
    padding-top: 0;
    grid-row: 2 / 3;
    grid-column: 1 / -1;
    margin: 0 auto;
    text-align: center;
    max-width: 90%;
  }

  .article-pictures {
    margin: 0 auto;
    width: 90%;
  }

  .img-1 {
    grid-column: 1 / 3;
    grid-row: 3 / 4;
  }

  .img-2 {
    grid-column: 1 / 3;
    grid-row: 4 / 5;
  }

  .img-3 {
    grid-column: 1 / 3;
    grid-row: 5 / 6;
  }

  .img-4 {
    grid-column: 1 / 3;
    grid-row: 6 / 7;
  }
}
